import { showPage } from './pageBuilder';

// Selectez butonul "Load More"
const loadMoreButton = document.querySelector('.load-more');

// Iniializai numrul paginii curente
let currentPage = 1;

// Definim funcia loadMoreMovies pentru a ncrca mai multe filme atunci cnd se apas pe buton
const loadMoreMovies = async () => {
  currentPage++; // Incrementez numrul paginii pentru a solicita urmtoarea pagin de filme

  try {
    // Afisez urmtoarea pagin folosind funcia showPage
    window.scrollTo({ top: 0, behavior: 'smooth' }); // Acesta va face o micare lin ctre partea de sus a paginii
    await showPage(currentPage, false); // `false` indic c nu este o cutare, ci solicitarea pentru filme populare
    // Actualizai textul din div cu numrul paginii curente
    const currentPageDiv = document.getElementById('currentPage');
    currentPageDiv.innerText = `Page ${currentPage}`;
  } catch (error) {
    console.error('Error loading more movies:', error);
  }
};

// Adugai un eveniment de ascultare pentru clic pe butonul "Load More"
loadMoreButton.addEventListener('click', loadMoreMovies);

// Exportai funcia loadMoreMovies pentru a o face disponibil pentru import
export { loadMoreMovies };
