import axios from 'axios';

const URL = 'https://api.themoviedb.org/3';
const API_KEY = '24996ba1a77f5ecd2a2a683701ea072b';

// Funcie pentru construirea URL-urilor

const constructUrl = (path, params = {}) => {
  const queryParams = new URLSearchParams(params); // Creeaz un obiect pentru gestionarea parametrilor URL
  queryParams.set('api_key', API_KEY); // Adaug cheia API la parametrii URL
  return `${URL}${path}?${queryParams.toString()}`; // Construiete URL-ul final
};

// Funcie pentru efectuarea cererilor la API
const fetchData = async (path, params = {}) => {
  try {
    const url = constructUrl(path, params); // Construiete URL-ul pentru cerere
    const response = await axios.get(url); // Efectueaz cererea la API folosind Axios
    return response.data; // Returneaz datele obinute din rspunsul API
  } catch (error) {
    console.error(`Error fetching data from ${path}:`, error); // Gestioneaz erorile i afieaz mesaje de eroare
    throw error; // Arunc eroarea mai departe pentru a fi gestionat ulterior
  }
};

export { constructUrl, fetchData };
