import '../src/js/header';
import '../src/js/footer';
import './js/movieButtons';

import { fetchGenreList } from './js/fetchGenreList';
import { showPage } from './js/pageBuilder';
import { fetchPopularMovies } from './js/fetchMovieApi';
import { loadMoreMovies } from './js/loadMore';
import '../src/js/search';
import { handleResponse } from './js/galleryBuilder';

// Funcia de iniializare a aplicaiei
const startingPagination = async () => {
  try {
    showPage(1); // Afieaz pagina curent (pagina 1)
    const genreList = await fetchGenreList(); // Obtin lista de genuri (ACTIUNE, COMEDIE ETC)
    const popularMovies = await fetchPopularMovies(1); // Obtin lista de filmele populare pentru pagina curent (pagina 1)
    handleResponse(popularMovies, true, genreList); // Manipulez rspunsul API-ului cu funcia handleResponse
  } catch (error) {
    console.error('Error', error); // Afisam erorile n caz de problema
  }
};

startingPagination(); // Apelul funciei de iniializare a aplicaiei

// Adugai un eveniment de ascultare pentru clic pe butonul "Load More"
const loadMoreButton = document.querySelector('.load-more');
loadMoreButton.addEventListener('click', loadMoreMovies); // Cnd se face clic pe buton, se apeleaz funcia loadMoreMovies
