import { Notify } from 'notiflix/build/notiflix-notify-aio';

import TheMovieDbApi from './themoviedb-api';
import { clearGallery } from './clearGallery';
import { handleResponse } from './galleryBuilder';

const form = document.querySelector('.search-form');

const api = new TheMovieDbApi();

form.addEventListener('submit', onSearchFormSubmit);

function onSearchFormSubmit(event) {
  event.preventDefault();

  const queryString = event.currentTarget.elements.searchText.value.trim();

  if (!queryString) {
    // Afieaz notificare pentru a specifica c trebuie s introduci un nume
    Notify.failure('You must enter a movie name.');
    return;
  }

  api
    .getMoviesByName(queryString)
    .then(res => {
      console.log(res);
      if (res.results.length < 1) {
        Notify.failure(
          'Search result not successful. Enter the correct movie name.'
        );
      } else {
        Notify.success('Results found successfully.');
        clearGallery(); // terge galeria nainte de a afia rezultatele cutrii
        handleResponse(res, true); // Afieaz rezultatele cutrii n galerie
      }
    })
    .catch(err => {
      Notify.failure('A aprut o eroare n timpul cutrii.');
    })
    .finally(() => form.reset());
}
