import { fetchData } from './apiUtils';

// Funcie pentru obinerea listei de filme populare n funcie de pagin
const fetchPopularMovies = async page => {
  const params = { page: page };
  return fetchData('/trending/movie/week', params); // Efectueaz cererea pentru filme populare
};

// Funcie pentru obinerea detaliilor unui film specific n funcie de ID
const fetchMovieDetails = async movieId => {
  const params = { language: 'en-US' };
  return fetchData(`/movie/${movieId}`, params); // Efectueaz cererea pentru detalii de film
};

export { fetchPopularMovies, fetchMovieDetails };
